* {
  box-sizing: border-box;
}

html {
  color: #222;
  font-size: 1em;
  line-height: 1.4;
}

body {
  color: #44484a;
  text-rendering: optimizeLegibility;
  font-size: 18px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Android Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  line-height: 1.5;
  height: 100%;
  min-height: 100vh;
  margin: 0 auto;
  overflow-y: scroll;
  visibility: visible;
  word-break: break-word;
  word-wrap: break-word;
}

body,
input,
button {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1, h2, h3, h4, h5, h6 {
  margin: 20px 0 14px 0;
  color: #262626;
  font-weight: 600;
  line-height: 1.25;
}

h1 {
  font-size: 2em;
}

h2 {
  font-size: 1.5em;
}

h3 {
  font-size: 1.17em;
}

h4 {
  font-size: 1em;
}

h5 {
  font-size: 0.83em;
}

h6 {
  font-size: 0.75em;
}

p {
  line-height: 1.5;
  margin: 15px 0;
}

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ddd;
  margin: 1em 0;
  padding: 0;
  overflow: hidden;
  background: transparent;
}

/*
 * Remove the gap between audio, canvas, iframes,
 * images, videos and the bottom of their containers:
 * https://github.com/h5bp/html5-boilerplate/issues/440
 */
audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
}

del {
  text-decoration: line-through;
  color: #999;
}

ins {
  color: #000;
}

img {
  max-width: 100%;
}

/* emojis never get resized */
img.emoji {
  width: 20px !important;
  margin: 0px !important;
  float: none !important;
  border: none !important;
}

.img--circle {
  border-radius: 50%;
}

a {
  text-decoration: underline;
}

a:link, a:visited {
  text-decoration: underline;
  color: #000;
}

a:hover, a:active {
  text-decoration: underline;
  color: #000;
}

b,
strong {
  font-weight: bold;
}

em {
  font-style: italic;
}

pre {
  padding: 16px;
  overflow: auto;
  line-height: 1.45;
  background-color: #f6f8fa;
  border-radius: 3px;
  word-wrap: normal;
  margin-top: 0;
  margin-bottom: 16px;
}

code {
  font-family: Menlo, Monaco, Consolas, "Liberation Mono", "Roboto Mono", "Courier New", Courier, monospace !important;
  font-size: 0.9em;
  padding: 0.2em 0.4em;
  margin: 0;
  background-color: rgba(27, 31, 35, 0.05);
  border-radius: 3px;
}

pre code {
  display: inline;
  max-width: auto;
  padding: 0;
  margin: 0;
  overflow: visible;
  line-height: inherit;
  word-wrap: normal;
  background-color: transparent;
  border: 0;
}

pre > code {
  padding: 0;
  margin: 0;
  word-break: normal;
  white-space: pre;
  background: transparent;
  border: 0;
}

blockquote {
  font-style: italic;
  border-left: 2px solid #000;
  padding-left: 15px;
  margin-left: 0;
}

ul,
ol {
  padding: 0;
  margin: 0 0 10px 10px;
  padding-left: 5px;
  list-style-position: outside;
  display: block;
}

ul li ul,
ul li ol {
  padding: 0;
  margin: 0 0 0px 10px;
  padding-left: 5px;
  list-style-position: outside;
  display: block;
  font-size: inherit;
}

ul {
  list-style-type: disc;
}

ol {
  list-style-type: decimal;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
}

td,
th {
  padding: 0;
}

table {
  margin-top: 0;
  margin-bottom: 16px;
  display: block;
  width: 100%;
  overflow: auto;
}

table tr {
  background-color: #fff;
  border-top: 1px solid #c6cbd1;
}

table th {
  font-weight: 600;
}

table th,
table td {
  padding: 6px 13px;
  border: 1px solid #dfe2e5;
}

/*
 * Remove default fieldset styles.
 */
fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

/*
 * Allow only vertical resizing of textareas.
 */
textarea {
  resize: vertical;
}

input[type=text],
input[type=password],
input[type=date],
input[type=datetime],
input[type=datetime-local],
input[type=month],
input[type=week],
input[type=email],
input[type=number],
input[type=search],
input[type=tel],
input[type=time],
input[type=url],
textarea {
  outline: 0px none;
  -webkit-appearance: none;
  -moz-appearance: none;
  color: rgba(0, 0, 0, 0.75);
  display: block;
  margin: 0;
  padding: 5px 3px;
  height: 25px;
  width: 100%;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  transition: border-color 0.15s ease-in-out 0s;
  -webkit-transition: border-color 0.15s ease-in-out 0s;
  -moz-transition: border-color 0.15s ease-in-out 0s;
  -ms-transition: border-color 0.15s ease-in-out 0s;
  -o-transition: border-color 0.15s ease-in-out 0s;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
  border: 1px solid #e3e3e3;
}

textarea {
  height: 71px;
}

input[type=text]:focus,
input[type=password]:focus,
input[type=date]:focus,
input[type=datetime]:focus,
input[type=datetime-local]:focus,
input[type=month]:focus,
input[type=week]:focus,
input[type=email]:focus,
input[type=number]:focus,
input[type=search]:focus,
input[type=tel]:focus,
input[type=time]:focus,
input[type=url]:focus,
textarea:focus {
  border-color: #4492ff;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
  outline: 0px none;
}

button {
  cursor: pointer;
  border-radius: 0;
}

details summary {
  cursor: pointer;
}
details:not([open]) > *:not(summary) {
  display: none;
}

.button {
  -webkit-border-radius: 3px;
  border-radius: 3px;
  -webkit-box-shadow: 0 1px 0 rgba(0, 0, 0, 0.05);
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.05);
  -webkit-transition: background-color 0.218s, border-color 0.218s, box-shadow 0.218s;
  transition: background-color 0.218s, border-color 0.218s, box-shadow 0.218s;
  -webkit-user-select: none;
  user-select: none;
  background-color: #fff;
  border: 1px solid #d9d9d9;
  cursor: pointer;
  height: 28px;
  line-height: 28px;
  padding: 0 10px 0 10px;
  outline: none;
  width: auto;
  background-image: none;
  -webkit-appearance: none;
  margin: 0;
}

label {
  margin-bottom: 5px;
  margin-top: 10px;
  display: block;
}

.button:hover {
  -webkit-box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
  border-color: #bfbfbf;
}

.button:active {
  -webkit-box-shadow: inset 0 1px 0 #ddd;
  box-shadow: inset 0 1px 0 #ddd;
  background-color: #e5e5e5;
  border-color: #bfbfbf;
}

.form-button {
  padding-top: 20px;
  margin-bottom: 40px;
}

::-webkit-input-placeholder {
  vertical-align: middle;
  padding: 3px 2px;
}

:-moz-placeholder {
  /* Firefox 18- */
  vertical-align: middle;
  margin: 3px 2px;
}

::-moz-placeholder {
  /* Firefox 19+ */
  vertical-align: middle;
  margin: 3px 2px;
}

:-ms-input-placeholder {
  vertical-align: middle;
  margin: 3px 2px;
}

/* /TYPOGRAPHY */
main {
  width: 740px;
  min-height: 50vh;
  margin: 0 auto;
}

.header {
  width: 740px;
  margin: auto;
  text-align: center;
  padding-top: 3rem;
  padding-bottom: 3rem;
  height: 56px;
}

.header .header__search-box {
  width: 300px;
  margin: 0 auto;
}

.header a {
  text-decoration: none;
  font-weight: bold;
}

.logo {
  width: 80px;
}

.header__navigation {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.navigation {
  font-size: 0.9em;
}

nav ul {
  list-style-type: none;
  padding: 0;
  margin: 0 0;
  list-style-position: outside;
  display: block;
}

nav ul li {
  display: inline;
}

nav ul li:after {
  content: "";
  margin: 0 5px;
  color: #aaaaaa;
}

nav ul li:last-child:after {
  content: "";
}

.posts-list {
  list-style: none outside;
  margin: 0;
}

.posts-list a {
  text-decoration: none;
}

.posts-list-item {
  position: relative;
}

.posts-list time {
  position: absolute;
  left: 0;
  top: 0;
}

.posts-list .post-link {
  display: block;
  padding-left: 4.5em;
}

#content {
  max-width: 740px;
  width: 740px;
}

#content article:first-child {
  margin-top: 0px;
}

#content article header img.author {
  width: 50px;
  /*height: 46px;
  border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -moz-border-radius: 50%;*/
  float: left;
  margin-right: 10px;
  margin-bottom: 15px;
}

#content article header strong {
  display: block;
  color: #262626;
}

.article__title {
  font-size: 2.5em;
  letter-spacing: -1.79px;
  line-height: 1.1em;
}

.article__meta {
  font-size: 0.75em;
  color: #aaadad;
}

.excerpt img {
  float: left;
  /*margin-top:15px;*/
  margin-right: 15px;
  margin-bottom: 15px;
  max-width: 120px;
  max-height: 120px;
}

.excerpt a.title {
  color: #4492ff;
  line-height: 1.4;
  font-size: 16px;
  font-style: normal;
  font-variant: normal;
  font-weight: 300;
  max-height: 157px;
  font-size-adjust: none;
  font-stretch: normal;
}

.excerpt .tags {
  list-style-type: none;
  display: inline;
}

.excerpt .tags li {
  display: inline;
  background-color: rgba(0, 0, 0, 0.05);
  background-color: white;
}

.excerpt .tags li:after {
  content: ", ";
  color: rgb(153, 153, 153); /*\00b7*/
}

.excerpt .tags li:last-child:after {
  content: "";
}

.article__excerpt {
  font-style: italic;
  padding: 10px;
}

footer {
  text-align: center;
  margin-top: 6rem;
  padding-bottom: 4rem;
  color: #596a73;
}

.copyright {
  font-size: 75%;
}

.disclaimer {
  font-size: 75%;
}

.intro {
  margin-top: 4rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.intro__image {
  margin: auto;
  margin-top: 120px;
  width: 200px;
}

.intro__title {
  margin-bottom: 50px;
  text-align: center;
}

.intro__lead {
  color: #aaa;
  font-size: 1.5em;
  margin-top: 30px;
  font-weight: 300;
  font-style: italic;
  margin-bottom: 120px;
  margin-top: 120px;
}

/* Social */
.social {
  list-style-type: none;
  text-align: center;
  list-style-position: outside;
  display: block;
  padding: 0;
  margin: 0;
}

.social li {
  display: inline;
  padding: 0 5px;
}

.social li a {
  display: inline-block;
  width: 35px;
  height: 35px;
  color: #404040;
}

.social svg {
  fill: #848d95;
  width: 35px !important;
  height: 35px !important;
}

aside {
  display: none;
}

/* Sharing */
.sharing a {
  text-decoration: none;
}

/* Pagination */
.pagination {
  width: 100%;
  margin-bottom: 20px;
}

/* Projects */
.project div.description p {
  margin: 10px 0;
}

.project dl {
  margin: 0;
  font-size: 0.9em;
}

.project dl dt {
  width: 130px;
  float: left;
  height: 22px;
}

.project dl dd {
  height: 22px;
}

.project h2.approved {
  background: url(approved.png) 6px 8px no-repeat;
  margin-left: -30px;
  padding-left: 30px;
}

.project p.approved {
  color: #888;
  font-size: 14px;
}

.certification-logo {
  width: 300px;
}

/** images with caption */
.image-wrapper {
  text-align: center;
}
.image-wrapper .image-caption {
  color: #828282;
  margin-top: 10px;
}

/* contact form */
.alert-success {
  color: green;
}

.alert-danger {
  color: red;
}

/** adds */
.add {
  display: block !important;
  max-width: 420px;
  width: 420px;
  margin: 15px 15px 0 15px;
  padding: 15px;
  height: 100px;
  max-height: 100px;
  background-color: white;
}

/* Media Queries */
@media screen and (max-width: 740px) {
  .header, main {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  .header {
    padding-top: 1rem;
    padding-bottom: 1rem;
    height: 100px;
  }
  .header__navigation {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    gap: 0.5rem;
  }
  .intro {
    flex-direction: column-reverse;
  }
  #side, #content {
    flex: 1;
    -webkit-box-flex: 1 auto;
    -moz-box-flex: 1 auto;
    -ms-flex: 1 auto;
    width: 100%;
    max-width: 100%;
    position: relative;
  }
